
import React, { useState, useEffect } from 'react';
import Wrapper from '../common/Wrapper';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import result from '../../pages/contents/result';
import styled from 'styled-components';
import LoadingBar from '../common/LoadingBar';
import '../common/Main.css';
import Footer from '../common/Footer';
import RadarChart from './Radarchart';
import Button from '../common/Button';
import { useReactToPrint } from 'react-to-print';
import { useRef } from 'react';
import DepressionChart from './DepressionChart';
import AnxietyChart from './AnxietyChart';

const Result = styled.div`
  text-align : center;
  margin : 0;
  padding : 0;
  li {
    margin-top : 7px;
  }
`;

const useStyles = makeStyles({
    title: {
        fontSize: 30,
        marginBottom: '10px',
        marginTop: '10px',
        textAlign: "center",
        fontFamily: 'TTTogether',
        color: '#5887ff',
    },
    name: {
        fontSize: 30,
        marginBottom: '10px',
        marginTop: '-10px',
        textAlign: "center",
        color: 'black',
        fontFamily: 'TTTogether',
        fontWeight: '400',
    },
    button: {
        textAlign: "center",
        fontFamily: 'TTTogether',
        marginTop: '5%',
    },
});

const Img = styled.img`
    width : 200px;
    height : 200px;
    border-radius : 50%;
    border : 1px solid white;
  `;

const Textbox = styled.div`
    padding : 30px;
    background-color : white;
    border-radius: 10px;
    margin-bottom : 20px;
  `;
const Subtitle = styled.div`
    color : black;
    font-family: 'TTTogether';
    font-size : 1.5rem;
    margin-bottom : 10px;
    text-align : left;
  
  `
const Text = styled.div`
  color : black;
  text-align : left;
  font-family: 'IBMPlexSansKR-Regular';
`

const Resultcard = (data) => {
    const [showResult, setShowResult] = useState(true); //false 로 바꾸기
    const classes = useStyles();
    const total_score = {
        'depression_score' : 100 - Math.ceil(data.data.depression * 3.70),
        'anxiety_score' : 100 - Math.ceil(data.data.anxiety * 4.76),
        'esteem_score' : Math.ceil(data.data.esteem * 3.3),
        'resilience_score' : Math.ceil(data.data.resilience * 2.5),
        'icq_score' : Math.ceil(data.data.icq * 1.66)    
    }
    const componentRef = useRef(null);

    const CustomDate = () => {

        const today = new Date();
        const formattedDate = `${today.getFullYear()}년 ${today.getMonth() + 1}월 ${today.getDate()}일`;
            return (
                <div>
                    <div>{formattedDate}</div>
                </div>
            );
        }
        
    
    useEffect(() => {
        const newLocal = require(`../../img/brain.png`);
        const tick = setTimeout(() => {
            setShowResult(true);
        }, 3000);
        return () => clearTimeout(tick);
    }, []);


    const goMentalPt = () => {
        window.open('http://mentalpt.co.kr/');
    }

    function DepressionMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score <= 4) {
            level = 'None-minimal';
        } else if (score >= 5 && score <= 9) {
            level = 'mild';
        } else if (score >= 10 && score <= 14) {
            level = 'moderate';
        } else if (score >= 15 && score <= 19) {
            level = 'moderately severe';
        } else {
            level = 'severe';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.depression.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>우울 ({total_score.depression_score}점)</Subtitle><br></br>
                <DepressionChart score={total_score.depression_score}></DepressionChart>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }

    function AnxietyMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score <= 4) {
            level = 'None-minimal';
        } else if (score >= 5 && score <= 9) {
            level = 'mild';
        } else if (score >= 10 && score <= 14) {
            level = 'moderate';
        } else {
            level = 'severe';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.anxiety.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>불안 ({total_score.anxiety_score}점)</Subtitle><br></br>
                <AnxietyChart score={total_score.anxiety_score}></AnxietyChart>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }
    function SelfEsteemMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score <= 25) {
            level = 'low';
        } else if (score >= 26 && score < 30) {
            level = 'medium';
        } else {
            level = 'high';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.esteem.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>자존감 ({total_score.esteem_score}점)</Subtitle><br></br>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }
    function ResilienceMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score < 20) {
            level = 'low';
        } else if (score >= 20 && score < 30) {
            level = 'medium';
        } else {
            level = 'high';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.resilience.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>탄력성 ({total_score.resilience_score}점)</Subtitle><br></br>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }
    function ICQMessage({ score }) {
        let level;
        // Determine the depression level based on the score
        if (score >= 0 && score < 30) {
            level = 'low';
        } else if (score >= 30 && score < 45) {
            level = 'medium';
        } else {
            level = 'high';
        }
    
        // Find the corresponding result based on the determined level
        const selectedResult = result.icq.find(item => item.message === level);
    
        if (!selectedResult) {
            return <div>Result not found</div>;
        }
    
        return (
            <Textbox>
                <Subtitle>대인관계 ({total_score.icq_score}점)</Subtitle><br></br>
                <Text dangerouslySetInnerHTML={{__html: selectedResult.result}}></Text>
            </Textbox>
        );
    }
    const handlePrint = useReactToPrint({
        documentTitle: '멘탈점수',
        onBeforePrint: () => console.log('저장 중...'),
        onAfterPrint: () => console.log('저장 완료'),
        removeAfterPrint: true,
    });

    return (
        <>
        <Wrapper>
            {showResult &&
                <Result ref={componentRef} style={{backgroundColor: 'white', paddingTop:'20px'}}>
                    <Typography className={classes.title}>내 멘탈 점수</Typography>
                    <CustomDate></CustomDate>
                    <RadarChart score={total_score}/>
                    <Typography className={classes.name} style = {{ color : "#5887ff"}}>종합 {Math.ceil(total_score.anxiety_score + total_score.depression_score + total_score.esteem_score + total_score.icq_score + total_score.resilience_score) / 5}점</Typography>
                    <Text style={{textAlign: 'center'}}>※ 모든 항목은 점수가 높을 수록 <br></br> 정신이 건강한 상태입니다.</Text>
                    <DepressionMessage score={data.data.depression}/>
                    <AnxietyMessage score={data.data.anxiety}/>
                    <Text style={{textAlign: 'center'}}>※ 자존감, 회복 탄력성, 대인관계의 <br></br> 점수는 높을수록 좋습니다.</Text>
                    <SelfEsteemMessage score={data.data.esteem}/>
                    <ResilienceMessage score={data.data.resilience}/>
                    <ICQMessage score={data.data.icq}/>
                    <Typography className={classes.name} style={{fontSize: '20px'}}>멘탈 점수를 높이고 싶다면?</Typography>
                    <Button onClick={goMentalPt}  style = {{padding: '5px 5px',background : "#407aff", margin: "30px 0", fontSize:'20px'}}> MentalPT 이동하기</Button>
                    <Button onClick={()=> {
                        handlePrint(null, () => componentRef.current);
                    }} style = {{padding: '5px 5px',background : "#407aff", margin: "20px 0", fontSize:'20px'}}>
                        결과 저장하기</Button>
                </Result>
            }
            {!showResult &&
                <LoadingBar done={100} />
            }
        </Wrapper>
        {showResult && <Footer />}
        </>
    );
};

export default Resultcard;