import React, { useState, useRef, useEffect } from 'react';
import { Chart } from 'chart.js';

function DepressionChart({ score }) {
    const chartRef = useRef(null);
    const data = {
        labels: ['기준'],
        datasets: [{
            label: '심각한 우울증',
            data: [44],
            backgroundColor: 'rgba(255, 11, 0, 0.5)',
            borderColor: 'rgba(255, 11, 0, 0.5)',
            stack: 'stack1'
        },
        {
            label: '우울증',
            data: [37],
            backgroundColor: 'rgba(255, 239, 0, 0.5)',
            borderColor: 'rgba(255, 239, 0, 0.5)',
            stack: 'stack1'

        },
        {
            label: '우울증 없음',
            data: [19],
            backgroundColor: 'rgba(5, 0, 255, 0.5)',
            borderColor: 'rgba(5, 0, 255, 0.5)',
            stack: 'stack1'
        },
        {
            label: '내 점수',
            data: [score],
            backgroundColor: 'rgba(67, 245, 39, 0.8)',
            borderColor: 'rgba(67, 245, 39, 0.8)',
            stack: 'stack2'
        },
    ]
    }

    const customBar = {
        id: 'customBar',
        beforeDatasetsDraw(chart, args, pluginOptions){
            const {ctx, config, data, chartArea: {top, bottom, left, right, width, height}, scales: {x,y} } = chart;

            ctx.save();

            const datapointArray = data.datasets.map((dataset, index) => {
                return dataset.data[0]
            })
            
            const accumulate = array => array.map((sum => value => sum += value)(0));
            const cumulativeSumArray = accumulate(datapointArray);
            
            ctx.font = '12px sans-serif';
            ctx.fillStyle = '#666';
            ctx.fillText(data.datasets[0].label, 35, 60);

            ctx.font = '12px sans-serif';
            ctx.fillStyle = '#666';
            ctx.fillText(data.datasets[1].label, 160, 60);

            ctx.font = '12px sans-serif';
            ctx.fillStyle = '#666';
            ctx.fillText(data.datasets[2].label, 225, 60);

            console.log(x.getPixelForValue(data.datasets[3].data[0]))

            ctx.font = '12px sans-serif';
            ctx.fillStyle = '#666';
            ctx.fillText('내 점수', x.getPixelForValue(data.datasets[3].data[0]) - 50, 122);
        }
    }
    const config = {
        type: 'bar',
        data: data,
        options: {
            indexAxis: 'y',
            borderSkipped: false,
            borderWidth: 1,
            barPercentage: 0.5,
            categoryPercentage: 1,
            scales: {
                x: {
                    max: 100,
                    stacked: true,
                    grid: {
                        display: true,
                        drawBorder: false,
                        drawTicks: false,
                    },
                    ticks: {
                        display: true,
                    }
                },
                y: {
                    beginAtZero: true,
                    stacked: true,
                    grid: {
                        display: false,
                        drawBorder: false,
                        drawTicks: false,
                    },
                    ticks: {
                        display: false,
                    }
                }
            },
            events: [],
            plugins: {
                legend:{
                    display: false,
                }
            }
        },
        plugins: [customBar]
    }
    useEffect(() => {
        if (chartRef && chartRef.current) {
            const ctx = chartRef.current.getContext('2d');

            new Chart(ctx, config);
        }
    }, [score]);

    return <canvas style={{width: '100%', marginBottom: '30px'}}  ref={chartRef}></canvas>;
}

export default DepressionChart;